// Error page
.wrapper {
    margin: 50px;
    display: flex;
    flex-direction: column;
    align-items: center;
  
    h1 {
      font-weight: bold;
      color: $primary;
      font-size: 288px;
    }
    h2 {
      font-weight: 500;
      color: $primary;
      font-size: 36px;
    }
  
    .err-back {
      margin: 50px;
    }
  
    .backlink {
      color: $primary;
      font-size: 18px;
    }
  }
  
  @media (max-width: 980px) {
    .wrapper {
      margin: 30px;
      display: flex;
      flex-direction: column;
      align-items: center;
  
      h1 {
        font-weight: bold;
        color: $primary;
        font-size: 120px;
      }
      h2 {
        font-weight: 500;
        color: $primary;
        font-size: 20px;
        text-align: center;
      }
  
      .err-back {
        margin: 50px 0;
        text-align: center;
        a {
          font-size: 15px;
        }
      }
  
      .backlink {
        color: $primary;
        font-size: 18px;
      }
    }
  }