.accordion {
    margin-top: 50px;
    display: flex;
    align-items: center;
    flex-direction: column;
  }
  
  @media (max-width: 980px) {
    .accordion {
      margin-top: 20px;
    }
    .about {
      .top {
        img {
          min-height: 220px;
          object-fit: cover;
        }
      }
    }
  }