.housing {
    .content {
      display: flex;
      justify-content: space-between;
      margin: 20px 5%;
  
      &--left {
        display: flex;
        flex-direction: column;
        justify-content: center;
        .titles {
          h1 {
            color: $primary;
            font-size: 36px;
          }
          h2 {
            color: $primary;
            font-size: 18px;
          }
        }
        .tags {
        }
      }
      &--right {
        .host {
          display: flex;
          align-items: center;
  
          p {
            margin: 5px;
            color: $primary;
            font-size: 18px;
          }
  
          img {
            margin: 5px;
            width: 64px;
            border-radius: 50%;
          }
        }
      }
    }
  
    .accordions {
      display: flex;
      justify-content: center;
      .description {
        display: flex;
        justify-content: center;
        width: 50%;
        margin-bottom: 250px;
      }
      .equipement {
        display: flex;
        justify-content: center;
        width: 50%;
        margin-bottom: 250px;
  
        li {
          list-style: none;
        }
      }
    }
  }
  @media (max-width: 980px) {
    .housing {
      .content {
        display: flex;
        flex-direction: column;
        margin: 20px;
  
        &--left {
          .titles {
            h1 {
              font-size: 20px;
              font-weight: 500;
            }
            h2 {
              font-size: 16px;
              font-weight: 500;
            }
          }
        }
        &--right {
          display: flex;
          align-items: center;
          justify-content: space-between;
  
          .host {
            order: 2;
  
            p {
              font-size: 16px;
              text-align: right;
            }
  
            img {
              width: 42px;
            }
          }
          .rating {
            order: 1;
            img {
              width: 25px;
            }
          }
        }
      }
      .accordions {
        flex-direction: column;
        .description {
          display: flex;
          width: 100%;
          margin-bottom: 0px;
        }
        .equipement {
          display: flex;
          width: 100%;
          margin-bottom: 250px;
        }
      }
    }
  }