.accordion-item {
    margin-top: 20px;
    width: 80%;
    color: white;
    cursor: pointer;
    border: none;
    text-align: left;
    outline: none;
    font-size: 24px;
    font-weight: 500;
    border-radius: 5px;
    &:nth-child(4) {
      margin-bottom: 250px;
    }
  
    .accordion-title {
      background-color: $primary;
      display: flex;
      justify-content: space-between;
      padding: 10px;
      border-radius: 5px;
  
      .arrow-down {
        transform: rotate(180deg);
        transition: 0.4s;
      }
    }
    .accordion-content {
      color: $primary;
      font-size: 22px;
      font-weight: 400;
      background-color: $background;
      padding: 10px;
      border-radius: 5px;
      transition: 0.4s;
    }
  }
  
  @media (max-width: 980px) {
    .accordion-item {
      font-size: 18px;
  
      .accordion-content {
        font-size: 16px;
      }
    }
  }