.tags {
    display: flex;
    margin: 5px;
    .tag {
      margin: 5px;
      background-color: $primary;
      border-radius: 10px;
      padding: 5px 20px;
  
      p {
        color: white;
      }
    }
  }
  
  @media (max-width: 980px) {
    .tags {
      .tag {
        p {
          font-size: 10px;
        }
      }
    }
  }